import React from "react";
import Header from "./Header";
import classNames from "classnames";
import Register from "./RegisterTab";
import LoginTab from "./LoginTab";
import FacebookIcon from '@material-ui/icons/Facebook';


function Login() {
  const containerClasses = classNames("container", "mt-5");
  const cardClasses = classNames("card", "social-block");
  const btnClasses = classNames("btn", "btn-block", "btn-social", "btn-google");
  const fbIconClasses = classNames("fab", "fa-facebook");
  const gIconClasses = classNames("fab", "fa-google");
  return (
    <div>
      <Header />
      <div className={containerClasses}>
        <div className="row">
        {/* <h1> Register </h1> */}
          <div className="col-sm-6">
            <div className="card">
              <div className="card-body">
              <h3 class="card-title">Register</h3>
              <Register />
              </div>
            </div>
          </div>
          <div className="col-sm-6">
            <div className={cardClasses}>
              <div className="card-body">
              <h3 class="card-title">Login</h3>
                {/* <a className={btnClasses} href="/auth/facebook" role="button">
                <FacebookIcon />
                  Sign In with Facebook
                </a> */}
                <a className={btnClasses} href="/auth/google" role="button">
                  <i className={gIconClasses}> </i>
                  Sign In with Google
                </a>
              </div>
              <p style={{textAlign:"center"}}>OR</p>
              <div className="card-body">
              <LoginTab />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
